'use strict';

import format from '@stdlib/error-tools-fmtprodmsg';

// MAIN //

/**
* Polyfill for changing the current working directory in environments having no native support for doing so.
*
* @param {string} path - desired working directory
* @returns {Error} error object
*
* @example
* var err = chdir( __dirname );
* if ( err ) {
*     console.error( err.message );
* }
*/
function chdir() {
	return new Error( format('0nP0p') );
}

// EXPORTS //

export default chdir;
